  /* background: blue; */
}
.skill-bars .bar .info span {
  font-weight: 500;
  background: #81f5b1;
  color: #000;
  font-size: 17px;
  opacity: 0;
  -webkit-animation: showText 0.5s 1s linear forwards;
          animation: showText 0.5s 1s linear forwards;
}
@-webkit-keyframes showText {
  100% {
    opacity: 1;
  }
}
@keyframes showText {
  100% {
    opacity: 1;
  }
}
/* .html {
  background: blue;
} */
.skill-bars .bar .progress-line {
  height: 10px;
  /* width: 95%; */
  background: #f0f0f0; /*progress bar background*/
  /* position: relative; */
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  -webkit-transform-origin: left;
          transform-origin: left;
  border-radius: 10px;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.05), 0 1px #81f5b1;
  margin-top: 30px;
  -webkit-animation: animate 1s cubic-bezier(1, 0, 0.5, 1) forwards;
          animation: animate 1s cubic-bezier(1, 0, 0.5, 1) forwards;
}
@-webkit-keyframes animate {
  100% {
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
  }
}
@keyframes animate {
  100% {
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
  }
}
.bar .progress-line span {
  height: 100%;
  position: absolute;
  border-radius: 10px;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  -webkit-transform-origin: left;
          transform-origin: left;
  background: #6665ee;
  /* background: #81f5b1; */
  -webkit-animation: animate 1s 1s cubic-bezier(1, 0, 0.5, 1) forwards;
          animation: animate 1s 1s cubic-bezier(1, 0, 0.5, 1) forwards;
}

.bar .progress-line.html span {
  /* width: 90%; */
  margin-left: 0;

  /* margin-top: 20px; */
}

.progress-line span::before {
  position: absolute;
  content: "";
  top: -10px;
  right: 0;
  width: 0;
  border: 7px solid transparent;
  border-bottom-width: 0px;
  border-right-width: 0px;
  border-top-color: #000;
  opacity: 0;
  -webkit-animation: showText2 0.5s 1.5s linear forwards;
          animation: showText2 0.5s 1.5s linear forwards;
}
.progress-line span::after {
  position: absolute;
  top: -28px;
  right: 0;
  font-weight: 500;
  background: rgb(0, 0, 0);
  padding: 1px 8px;
  font-size: 12px;
  border-radius: 3px;
  opacity: 0;
  /* margin-top: 100px; */ /* percent show margin */
  -webkit-animation: showText2 1s 1.5s linear forwards;
          animation: showText2 1s 1.5s linear forwards;
  /* background: blue; */ /*% background*/
  color: #fff;
}
.skill-bars .bar {
  background: #3dff8e;
  box-shadow: 0 0 10px 5px #3af085;
